import SimpleLightbox from 'simplelightbox';
import 'simplelightbox/dist/simple-lightbox.min.css';

const createImageCardTemplate = imageCard => {
  return `
   <li class="gallery-item">
      <a class="gallery-link" href="${imageCard.original}">
        <img
          class="gallery-image"
          src="${imageCard.preview}"
          alt="${imageCard.description}"
        />
      </a>
    </li>
  `;
};

const createGallaryMakeup = images
  .map(imageCardInfo => createImageCardTemplate(imageCardInfo))
  .join('');

const imagesGalleryEl = document.querySelector('.gallery');
imagesGalleryEl.innerHTML = createGallaryMakeup;

const lightbox = new SimpleLightbox('.gallery a', {
  captionsData: 'alt',
  captionPosition: 'bottom',
  captionDelay: 250,
  overlayOpacity: 0.8,
});
